@keyframes pulseFirstArrow {
  from {
    opacity: 1;
    transform: translate3d(-50%, -50%, 1px)scale3d(1, 1, 1);
  }

  50% {
    opacity: .3;
  }

  to {
    opacity: 0;
    transform: translate3d(-50%, -50%, 1px)scale3d(2.5, 2.5, 2.5);
  }
}

@keyframes pulseSecondArrow {
  from {
    opacity: 1;
    transform: translate3d(-50%, -50%, 1px)scale3d(1, 1, 1);
  }

  50% {
    opacity: .3;
  }

  to {
    opacity: 0;
    transform: translate3d(-50%, -50%, 1px)scale3d(2.2, 2.2, 2.2);
  }
}

@keyframes pulseLastArrow {
  from {
    opacity: 1;
    transform: translate3d(-50%, -50%, 1px)scale3d(1, 1, 1);
  }

  50% {
    opacity: .3;
  }

  to {
    opacity: 0;
    transform: translate3d(-50%, -50%, 1px)scale3d(1.9, 1.9, 1.9);
  }
}

