.cloud-frame {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('./images/cloud-frame.png') 50% 50%/cover no-repeat;
    z-index: 2;
  }
  &__bg {
    position: relative;
    z-index: 1;
  }
}